@font-face {
  font-display: block;
  font-family: "taroify-number-keyboard";
  font-style: normal;
  font-weight: normal;
  src: url("./fonts/number-keyboard.eot?o8m3qv");
  src: url("./fonts/number-keyboard.eot?o8m3qv#iefix") format("embedded-opentype"), url("./fonts/number-keyboard.ttf?o8m3qv") format("truetype"), url("./fonts/number-keyboard.woff?o8m3qv") format("woff"), url("./fonts/number-keyboard.svg?o8m3qv") format("svg");
}
[class^=taroify-backspace], [class*=" taroify-backspace"],
[class^=taroify-keyboard-hide], [class*=" taroify-keyboard-hide"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "taroify-number-keyboard", serif !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  line-height: 1;
  text-transform: none;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  speak: never;
}

.taroify-backspace::before {
  content: "\e900";
}

.taroify-keyboard-hide::before {
  content: "\e901";
}

.taroify-number-keyboard {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: var(--number-keyboard-z-index, 100);
  width: 100%;
  padding-bottom: 44px;
  user-select: none;
  background-color: var(--number-keyboard-background-color, var(--gray-2, #f2f3f5));
}
.taroify-number-keyboard--with-title {
  border-radius: var(--number-keyboard-with-title-border-radius, 40px 40px 0 0);
}
.taroify-number-keyboard__header {
  position: relative;
  box-sizing: content-box;
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--number-keyboard-header-height, 68px);
  padding-top: var(--number-keyboard-header-padding-top, 12px);
  font-size: var(--number-keyboard-header-font-size, var(--font-size-lg, 32px));
  color: var(--number-keyboard-header-color, var(--gray-7, #646566));
}
.taroify-number-keyboard__title {
  display: inline-block;
  font-weight: normal;
}
.taroify-number-keyboard__title-left {
  position: absolute;
  left: 0;
}
.taroify-number-keyboard__body {
  display: flex;
  padding: 12px 0 0 12px;
}
.taroify-number-keyboard__keys {
  display: flex;
  flex: 3;
  flex-wrap: wrap;
}
.taroify-number-keyboard__hide {
  position: absolute;
  right: 0;
  display: flex;
  align-items: center;
  height: 100%;
  padding: var(--number-keyboard-hide-padding, 0 var(--padding-md, 32px));
  font-size: var(--number-keyboard-hide-font-size, 48px);
  color: var(--number-keyboard-hide-color, var(--text-link-color, var(--text-link-color, #576b95)));
  cursor: pointer;
  background-color: transparent;
  border: 0 none;
}
.taroify-number-keyboard__hide:active {
  opacity: var(--number-keyboard-hide-active-opacity, var(--number-keyboard-key-active-opacity, var(--active-opacity, var(--active-opacity, 0.6))));
}
.taroify-number-keyboard__sidebar {
  display: flex;
  flex: 1;
  flex-direction: column;
}
.taroify-number-keyboard--unfit {
  padding-bottom: 0;
}

.taroify-key {
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--number-keyboard-key-height, 96px);
  font-size: var(--number-keyboard-key-font-size, 56px);
  line-height: var(--number-keyboard-key-line-height, 1.5);
  cursor: pointer;
  background-color: var(--number-keyboard-key-background-color, var(--white, #fff));
  border-radius: var(--number-keyboard-key-font-size, var(--border-radius-lg, 16px));
}
.taroify-key--large {
  position: absolute;
  top: 0;
  right: 12px;
  bottom: 12px;
  left: 0;
  height: auto;
  font-size: var(--number-keyboard-large-key-font-size, var(--font-size-lg, 32px));
}
.taroify-key--active {
  background-color: var(--number-keyboard-key-active-background-color, var(--gray-3, #ebedf0));
}
.taroify-key--blue {
  color: var(--number-keyboard-button-color, var(--white, #fff));
  background-color: var(--number-keyboard-button-background-color, var(--primary-color, var(--blue, #1989fa)));
}
.taroify-key--blue.taroify-key--active {
  opacity: var(--number-keyboard-key-active-opacity, var(--active-opacity, var(--active-opacity, 0.6)));
}
.taroify-key__wrapper {
  position: relative;
  box-sizing: border-box;
  flex: 1;
  flex-basis: 33%;
  padding: 0 12px 12px 0;
}
.taroify-key__wrapper--wider {
  flex-basis: 66%;
}
.taroify-key .taroify-keyboard-hide {
  font-size: var(--number-keyboard-hide-font-size, 48px);
}
.taroify-key .taroify-backspace {
  font-size: var(--number-keyboard-backspace-font-size, 44px);
}
.taroify-key__loading-icon {
  color: var(--number-keyboard-button-color, var(--white, #fff));
}