.taroify-password-input {
  position: relative;
  margin: var(--password-input-margin, 0 var(--padding-md, 32px));
  user-select: none;
}
.taroify-password-input__security {
  display: flex;
  width: 100%;
  height: var(--password-input-height, 100px);
  cursor: pointer;
}
.taroify-password-input__security::after {
  border-radius: var(--password-input-border-radius, 12px);
}
.taroify-password-input__item {
  position: relative;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: var(--password-input-item-font-size, 40px);
  line-height: 1.2;
  background: var(--password-input-item-background-color, var(--white, #fff));
}
.taroify-password-input__item--mask {
  position: absolute;
  top: 50%;
  left: 50%;
  width: var(--password-input-mask-width, var(--password-input-mask-size, 20px));
  height: var(--password-input-mask-height, var(--password-input-mask-size, 20px));
  visibility: hidden;
  background: var(--password-input-mask-color, var(--black, #000));
  border-radius: 100%;
  transform: translate(-50%, -50%);
}
.taroify-password-input__cursor {
  position: absolute;
  top: 50%;
  left: 50%;
  width: var(--password-input-cursor-width, 2px);
  height: var(--password-input-cursor-height, 40%);
  background: var(--password-input-cursor-color, var(--text-color, var(--gray-8, #323233)));
  transform: translate(-50%, -50%);
  animation: var(--password-input-cursor-animation-duration, 1s) taroify-cursor-flicker infinite;
}

@keyframes taroify-cursor-flicker {
  from {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.taroify-password-input__feedback {
  margin-top: var(--password-input-feedback-margin-top, var(--padding-md, 32px));
  font-size: var(--password-input-feedback-font-size, var(--font-size-md, 28px));
  color: var(--password-input-feedback-color, var(--gray-6, #969799));
  text-align: center;
}
.taroify-password-input__feedback--primary {
  color: var(--password-input-feedback-primary-color, var(--primary-color, var(--blue, #1989fa)));
}
.taroify-password-input__feedback--info {
  color: var(--password-input-feedback-info-color, var(--info-color, var(--cyan, #2db7f5)));
}
.taroify-password-input__feedback--success {
  color: var(--password-input-feedback-success-color, var(--success-color, var(--green, #07c160)));
}
.taroify-password-input__feedback--warning {
  color: var(--password-input-feedback-warning-color, var(--warning-color, var(--orange, #ff976a)));
}
.taroify-password-input__feedback--danger {
  color: var(--password-input-feedback-danger-color, var(--danger-color, var(--red, #ee0a24)));
}